@import "../../variables";

.textContainer {
  p {
    color: $text-clr;
    font-size: $main-text-size;
    line-height: 1.5;
    margin: 10px 0;
  }
}

.wrong {
  background-color: $wrong-word;
} 

.selected {
  background-color: $selected-word;
  color: $if-have-state-clr;
}

.correct {
  background-color: $correct-word;
  color: $if-have-state-clr;
}
