@import "./variables";

.container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  height: 100vh;
  padding: 20px;
  margin: 0 auto;
  background-image: $bg-gradient;
}

.content {
  display: flex;
  flex-direction: column;
  width: 100%;
  max-width: 800px;
  padding: 20px;
  background-color: transparent;
  border-radius: 8px;
}

.inputSection {
  display: flex;
  align-items: center;
  margin-bottom: 20px;

  button {
    margin-left: 10px;
    padding: 10px;
    border: 1px solid $border-clr;
    border-radius: 4px;
    background-color: #fff;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: $button-text-size;
  }
}

@media (min-width: 768px) {
  .content {
    flex-direction: column;
    justify-content: center;
  }

  .input-section {
    flex-direction: row;
    align-items: center;

    button {
      width: auto;
      margin: 0;
    }
  }
} 
