@import "../../variables";

.input{
    flex: 1;
    padding: 10px;
    border: 1px solid $border-clr;
    box-shadow: 0 0 10px $input-box-shadow-clr;
    border-radius: 4px;
    font-size: $input-text-size;
    outline: none;
}

@media (min-width: 768px){
    .input{
        flex: 1;
        margin-right: 10px;
    }
}